@import "./variables";

.homeMainContainer {
    width: 100%;
    height: 80vh;
    position: relative;
    background-image: $invertedPrimaryColor;
    z-index: -11;
  .homePageContainer {
    height: 75vh;
    width: 80%;
    margin: 0 auto;
    overflow: hidden;

    .singlePhoto {
      position: relative;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      max-width: 100%;
      height: 100%;
 
    }
  }
  .contactUsContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 5vh;
    position: relative;
    .contactButtonLink {
      width: 30vw;
      height: 4vh;
      border-radius: 10px;
      text-transform: uppercase;
      outline: none;
      font-size: 0.6rem;
      cursor: pointer;
      .buttonLink {
        display: block;
        text-decoration: none;
        color: $sixthColor;
        width: 100%;
      }
    }
  }
}
